/* Allgemeine Stile */
body {
    background-color: black;
    color: white;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    width: 90%;
    max-width: 1200px;
    margin: auto;
}

.app-container {
    display: flex;
}

.left-side {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.right-side {
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

/* Navbar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
}

.logo {
    font-size: 24px;
    font-weight: bold;
}

.social-icons {
    display: flex;
    gap: 12px;
    font-size: 24px;
}

.social-icons a {
    color: white;
    text-decoration: none;
    display: flex;
    align-items: center;
}

.resume-button {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px 16px;
    background-color: #1E3A8A; /* Farbe für den Button */
    border-radius: 5px;
    color: white;
    text-decoration: none;
    transition: background-color 0.3s ease;
    font-size: 14px; /* Schriftgröße für den Resume-Text */
}

.resume-button:hover {
    background-color: #3B82F6; /* Hellere Farbe beim Hover */
}

/* Hero */
.hero {
    text-align: center;
    padding: 40px 20px;
}

.profile-img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: 3px solid gray;
}

.title {
    font-size: 28px;
    margin-top: 10px;
}

.location {
    color: gray;
}

.subtitle {
    margin-top: 10px;
}

.cta-button {
    background-color: blue;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    margin-top: 15px;
    cursor: pointer;
}

/* About */
.about {
    max-width: 800px;
    margin: auto;
    padding: 20px;
}

/* Skills */
.skills {
    padding: 20px;
}

.skill-category {
    margin-bottom: 20px;
}

.skill-list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.skill-badge {
    padding: 6px 12px;
    color: white;
    border-radius: 15px;
    font-size: 14px;
}
/* Experience Section */
.experience {
    padding: 40px 20px;
    max-width: 800px;
    margin: auto;
  }
  
  /* Erfahrungseintrag */
  .experience-item {
    background-color: rgba(255, 255, 255, 0.05); /* Leichtes transparentes Grau */
    padding: 20px;
    border-radius: 10px;
    margin-bottom: 30px;
    border-left: 3px solid #666; /* Linke Linie für Design */
  }
  
  /* Header mit Logo */
  .experience-header {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .experience-logo {
    width: 50px;
    height: 50px;
    border-radius: 5px;
  }
  
  .experience-title {
    font-size: 20px;
    font-weight: bold;
  }
  
  .experience-company {
    font-size: 16px;
    color: gray;
  }
  
  .experience-duration {
    font-size: 14px;
    color: #aaa;
  }
  
  /* Beschreibungsliste */
  .experience-description {
    margin-top: 10px;
    padding-left: 20px;
  }
  
  .experience-description li {
    margin-bottom: 8px;
  }
  
  /* Tags für Technologien */
  .experience-tags {
    margin-top: 15px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .experience-tag {
    background-color: #166534; /* Dunkelgrün */
    padding: 6px 12px;
    border-radius: 15px;
    color: white;
    font-size: 14px;
    font-weight: bold;
  }
  